<template>
    <nav>
        <div class="logo">
            <h1>coffee<p>blend</p></h1>
            </div>
            <font-awesome-icon icon="fa-bars" class="icon" @click="showNav"/>
            <div class="links" >
            <font-awesome-icon icon="fa-arrow-right" @click="hideNav" class="icon"/>
            <router-link @click="hideNav"  v-for = 'api in apis' :key="api.route" :to="api.route" ref = 'close'>{{ api.link }}</router-link> 
            </div>
        </nav>
    <router-view/>
</template>

<script>
import { ref, watch } from 'vue'
export default {
name: 'landingPage',
setup() {

const apis = ref([
    {link: 'Home' , route: '/'},
    {link: 'Menu' , route: '/menu'},
    {link: 'Services' , route: '/services'},
    {link: 'Blog' , route: '/blog'},
    {link: 'About' , route: '/about'},
    {link: 'shop' , route: '/shop'},
    {link: 'contact' , route: '/contact'}
    ])

    const showNav = () => {
        document.querySelector('.links').style.right = 0;
    }
    const hideNav = () => {
        document.querySelector('.links').style.right = '-100%';
    }

    const close = ref([]);





    return {apis , showNav, hideNav, close}
}
}
</script>

<style lang = 'scss'>
@import '../scss/_global.scss';
@import '../scss/variables.scss';
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
}

nav {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
    z-index: 200;
            .icon {
                display: none;
                }
                svg {
                    font-size: 25px;
                    color: $whiteColor;
                    margin-right: 10px;
                    cursor: pointer;
                    margin-bottom : 15px;
                }

    .links {
    padding: 10px;
    }
h1 {
    padding: 10px;
    font-weight: normal;
    text-transform: uppercase;
    text-align: center;
    width: 100px;
    color: #FFF;
    p {
    font-size: 14px ;
}
}
a {
    position:relative;
    transition: .5s;
    font-weight: bold;
    font-size: 14px;
    color: #FFF;
    padding: 10px;
    text-decoration: none;
    &::after {
        content: '';
        width: 0;
        height: 3px;
        background-color: $heavyBrown;
        position: absolute;
        transition: .3s;
        bottom: 0;
        transform: translateX(-85%);
        overflow: hidden;
    }
        &:hover {
            &::after {
                    width : 100%;
                }
            color: $heavyBrown;
            left: 0;
        }
    &.router-link-exact-active {
    color: $heavyBrown;
}

}

}
@media (max-width : 767px) {
nav {
    .icon {
        display: block;
    }
        .links {
        display: flex;
        flex-direction: column;
        position: fixed;
        right: -100%;
        top: 0;
        padding: 30px 50px; 
        overflow: hidden;
        background-color: $Brown;
        height: 100vh;
        transition: .5s ease-in-out;
    }   

}

}

</style>