<template>
    <div class="counter">
        <div class="boxs">
            <div class="box" 
            v-for="(icon, index) in icons"  
            :data-index = 'index' 
            :key = 'icon.number' 
            data-aos = 'fade-up' 
            data-aos-duration = '6000'
            >
                <div class="rotate">
                    <font-awesome-icon :icon = 'icon.font'/>
                </div>
                <p class="number" :data-number = 'icon.number' ref="items">{{ icon.number }}</p>
                <div class="title">{{ icon.text }}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted, ref, watch } from 'vue';
export default {
name: 'Counter',
setup() {
    const icons = ref([
        {
            font: 'fa-mug-hot',
            number: 100,
            text: 'Coffee Branches'
        },
        {
            font: 'fa-mug-hot',
            number: 85,
            text: 'Number of Awards'
        },
        {
            font: 'fa-mug-hot',
            number: 10567,
            text: 'Happy Customer'
        },
        {
            font: 'fa-mug-hot',
            number: 800,
            text: 'Stuff'
        },
    ])






    return { icons }
}
}
</script>

<style lang ='scss' scoped>
@import '../scss/variables';
.counter {
    padding: $padding 0;
    background-image: url('../assets/imgs/menu-2.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    position: relative;
    &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #00000085;
    top: 0;
    left: 0;
    }
    .boxs {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            position: relative;
            text-align: center;
            .box {
                margin: 0 20px;
            }
            @media (max-width: 767px) {
                display: block;
            }
            .rotate {
                width: 48px;
                margin: 10px auto;
                position: relative;
                padding: 13px;
                text-align: center;
                &::after {
                    content: "";
                    width: 100%;
                    height: 100%;
                    transition: 0.5s ease;
                    background: transparent;
                    position: absolute;
                    border: 1px solid $heavyBrown;
                    left: 0;
                    top: 0;
                }
            }
            svg {
                font-size: 25px;
                color: $heavyBrown;
            }
            .number {
                color: $heavyBrown;
                font-size: 17px;
                margin: 15px 0;
            }
            .title {
                font-size: 12px;
                color: $whiteColor;
            }
    }
}
</style>