<template>
<div class="contact">
<div class="box" data-aos = 'fade-left'>
        <div class="info" v-for="(text, index) in texts" :key='text.icon' :data-index = 'index'>
        <div class="detailes">
                <font-awesome-icon :icon = 'text.icon'/>
            <p>{{ text.detailes }}</p>
            <br>
            <p class="text">{{ text.content }}</p>
        </div>
    </div>
</div>
    <div class="table" data-aos = 'fade-right' data-aos-duration = '5000'>
                <h1>a book title</h1>
                    <p class="message" v-if="sent">Message sent!</p>
                <form @submit.prevent>
                    <input type="text" v-model="firstName" placeholder="First Name" required>
                    <input type="text" v-model="lastName" placeholder="Last Name" class="input" required>
                    <input type="date" v-model="date" class="date" required>
                    <input type="time" v-model="time" class="date" required>
                    <input type="text" v-model="phone" placeholder="phone" class="date" required>
                    <input placeholder="Message" v-model="message" type="text" required/>
                    <input type="submit" value="Appointment" @click="submit" class="button">
                </form>
    </div>
</div>
</template>

<style lang = 'scss' scoped>
@import '../scss/variables';
.contact {
    display: flex;
    position: relative;
    background-color: $blackColor;
    flex-wrap: wrap;
    .box {
    display: flex;
    justify-content: center;
    margin-left: 100px;
    flex-wrap: wrap;
    }
    .info {
        width: 210px;
        padding: 14px;
        display: flex;
        .detailes {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            svg {
                margin-bottom: 2px;
                color: $Brown;
                margin-right: 15px;
            }
            p {
                color: $whiteColor;
            }
            .text {
                color: #777;
                padding-top: 10px;
                margin-left: 36px;
            }
        }
    }
    .table {
        position: absolute;
        right: 0px;
        bottom: 00px;
        background-color: $Brown;
        width: 450px;
        padding: 15px;
        h1 {
            text-transform: uppercase;
            color: $whiteColor;
        }
        .message {
            background-color: $whiteColor;
            width: fit-content;
            padding: 10px;
            margin: 0 10px;
            border-radius: 6px;
            color: $heavyBrown;
        }
        input {
            width : 205px;
            padding: 15px 10px;
            margin: 10px 0;
            border: none;
            border-bottom: 1px solid $whiteColor;
            background-color: transparent;
            outline: none;
            color: $whiteColor;
            &::placeholder{
                color: $whiteColor;
            }
        }
        .input {
            margin-left: 10px;
        }
        .date {
            width: 32.3%;
            margin-right: 1%; 
        }
        .button {
                margin-left: 10px;
                background: #FFF;
                color: black;
                cursor: pointer;
        }
        .message {
            animation: animation 1s forwards;
            transition: all .5s ease-in-out;
        }
    }

    @keyframes animation {
        from {
            opacity: 0;
            transform: translateY(-10px);
        }
        to {
            transform: translateY(0);
        }
    }
    
@media (max-width: 1160px) {
    .contact {
        text-align: center;
    }
    .table {
        width : 425px;
        position: relative;
        text-align: center;
        input {
            width : 80%;
            max-width: 100%;
            margin-left: 0;
        }
    }
        .box {
            margin: 0;
        }
    }

    @media (min-width: 400px) and (max-width: 1160px ) {
        .box {
            margin: 0 auto;
        }
        .table {
            margin: 0 auto;
            margin-top: 15px;
        }
    }
}



</style>

<script>
import { ref } from 'vue';
export default {
    name: 'Contact',
    setup() {
        const sent = ref(false);
        const firstName = ref('');
        const lastName = ref('');
        const date = ref('');
        const time = ref('');
        const phone = ref('');
        const message = ref('');
        const texts = ref([
            {
                icon: 'fa-phone',
                detailes: '000 (123) 456 7890',
                content: 'A small river named flows by thier place and supplies'
            },
            {
                icon: 'fa-location-crosshairs',
                detailes: '198 West 21th Street',
                content: '203 Fake SL Mountain View, San Franscisco. California, USA'
            },
            {
                icon: 'fa-clock',
                detailes: 'Open Monday-Friday',
                content: '8:00pm - 9:0pm'
            },
        ]);

        const submit = () => {
            if (firstName.value && lastName.value && message.value) {
                sent.value = true;
            }
                firstName.value = '';
                lastName.value = '';
                date.value = '';
                time.value = '';
                phone.value = '';
                message.value = '';
            setTimeout(() => sent.value = false, 3000);
        }




        return {texts, sent, firstName , lastName , date, phone, time, message, submit}
    }

}
</script>