import { createApp } from 'vue'
import App from './App.vue'
import router from './router'

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import {
    faBars , faArrowRight , faPhone, faLocationCrosshairs , faClock, faListUl, faTruck
    ,faCoffee, faMugHot , faMessage, faCalendar, faUser, faEnvelope
} from '@fortawesome/free-solid-svg-icons'


import {
    faFacebook , faTwitter , faInstagram 
} from '@fortawesome/free-brands-svg-icons'

/* add icons to the library */
library.add(
    faBars ,
    faArrowRight,
    faPhone,
    faLocationCrosshairs,
    faClock,
    faListUl,
    faTruck,
    faCoffee,
    faMugHot,
    faMessage,
    faFacebook,
    faInstagram,
    faTwitter,
    faCalendar,
    faUser,
    faEnvelope
    )


/* add font awesome icon component */

import AOS from 'aos'
import 'aos/dist/aos.css'
import Aos from 'aos'

createApp(App).component('font-awesome-icon', FontAwesomeIcon).use(AOS.init()).use(router).mount('#app')
