<template>
    <div class="gallery">
        <div class="container">
            <div class="box" v-for = 'pic in pics' :key="pic" data-aos = 'zoomIn'>
                <img :src="getPic(pic)" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
export default {
    name: 'gallery',
    setup () {
        const pics = ref([
            'gallery-1.jpg',
            'gallery-2.jpg',
            'gallery-3.jpg',
            'gallery-4.jpg',
        ]);

const getPic = (src) => {
return require('../assets/imgs/' + src)
}


        return {pics , getPic}
    }
    }
</script>

<style lang="scss" scoped>
@import '../scss/variables';
.gallery {
    background-image: url('../assets/imgs/bg_4.jpg');
    background-size: cover;
    .container {
    display: flex;
    align-items: center;
    flex-wrap : wrap;            
    @media (max-width: 767px) {
            display: block;
        }
    }
    .box {
        overflow: hidden;
        width: 25%;
        position: relative;
                
                &:hover {
                    &::before {
                        left: 0;
                    }
                }
        &::before {
            content: 'Our Customers';
            display: flex;
            justify-content: center;
            align-items: center;
            color: $whiteColor;
            font-weight: bold;
            position: absolute;
            width: 100%;
            transition: .5s ease-in;
            height: 100%;
            left: -100%;
            right: 0;
            background-color: #c49b6369;
        }



            @media (max-width: 992px) {
                max-width: 100%;
                width: 50%;
            }
            @media (max-width: 767px) {
                width: 100%;
                max-width: 100%;
            }

        img {
            width: 100%;
        }
    }

}
</style>
